<template>

    <section
        class="section-inputs"
    >
        <div
            class="container-criterios"
        >
            <h2>
                {{ $t('mcdmTituloCriterios') }}
            </h2>

            <div
                class="label-container"
            >
                <label
                    class="label-simbolo"
                >
                    {{ $t('simbolo') }}
                </label>
                <label
                    class="label-nome"
                >
                    {{ $t('nome') }}
                </label>
            </div>

            <div
                v-for="(item, index) in criteriosLabelPrimeira"
                :key="index"
                class="label-criterios"
            >
                <label>
                    {{ index+1 }} - &ensp;
                </label>
                <input
                    class="simbolo-criterios input-simbolo"
                    v-model="criteriosSimboloPrimeira[index]"
                    maxlength="5"
                >
                <input
                    class="input-label input-criterios"
                    v-model="criteriosLabelPrimeira[index]"
                    maxlength="20"
                >
                <button
                    @click="removeCriterio(index)"
                    class="remove-button"
                > - </button>
                <button
                    @click="addCriterio()"
                    class="add-button"
                    v-if="index == criteriosLabelPrimeira.length - 1"
                > + </button>
            </div>
        </div>

        <!-- OPÇÕES E BOTÕES DE ADICIONAR OPÇÕES -->
        <div
            class="container-options"
        >
            <h2>
                {{ $t('mcdmTituloOptions') }}
            </h2>
            <div
                class="label-container"
            >
                <label
                    class="label-simbolo"
                >
                    {{ $t('simbolo') }}
                </label>
                <label
                    class="label-nome"
                >
                    {{ $t('nome') }}
                </label>
            </div>
            <div
                v-for="(item, index) in optionsLabelPrimeira"
                :key="index"
            >
                <label>
                    {{ index+1 }} - &ensp;
                </label>
                <input
                    class="simbolo-criterios input-simbolo"
                    v-model="optionsSimboloPrimeira[index]"
                    maxlength="5"
                >
                <input
                    class="input-options input-label"
                    v-model="optionsLabelPrimeira[index]"
                    maxlength="20"
                >
                <button
                    @click="removeOption(index)"
                    class="remove-button"
                > - </button>
                <button
                    @click="addOption()"
                    class="add-button"
                    v-if="index == optionsLabelPrimeira.length - 1"
                >+</button>
            </div>
        </div>

    </section>
</template>
<script>

export default {
    name: "vue-inputs-etapa-personalizado",
    data() {
        return {
            slideres: [],
            optionsLabelPrimeira: [
                "Option-1",
                "Option-2",
                "Option-3",
                "Option-4"
            ],
            optionsSimboloPrimeira: [
                "O1",
                "O2",
                "O3",
                "O4"
            ],
            criteriosLabelPrimeira: [
                "Criterio-1",
                "Criterio-2",
                "Criterio-3",
                "Criterio-4"
            ],
            criteriosSimboloPrimeira: [
                "C1",
                "C2",
                "C3",
                "C4"
            ]
        }
    },
    watch: {
        optionsLabelPrimeira: {
            handler(options) {
                this.noBlankText(options, "Option-")
            },
            deep: true
        },
        optionsSimboloPrimeira: {
            handler(options) {
                this.noBlankText(options, "O")
            },
            deep: true
        },
        criteriosLabelPrimeira: {
            handler(options) {
                this.noBlankText(options, "Criterio-")
            },
            deep: true
        },
        criteriosSimboloPrimeira: {
            handler(options) {
                this.noBlankText(options, "C")
            },
            deep: true
        }
    },
    created() {
        const getters = this.$store.getters
        if (getters.currentOptionsLabelPrimeira.length > 0) {
            this.optionsLabelPrimeira = getters.currentOptionsLabelPrimeira
        }
        if (getters.currentCriteriosLabelPrimeira.length > 0) {
            this.criteriosLabelPrimeira = getters.currentCriteriosLabelPrimeira
        }
        if (getters.currentOptionsSimboloPrimeira.length > 0) {
            this.optionsSimboloPrimeira = getters.currentOptionsSimboloPrimeira
        }
        if (getters.currentCriteriosSimboloPrimeira.length > 0) {
            this.criteriosSimboloPrimeira = getters.currentCriteriosSimboloPrimeira
        }
        this.updateStoreData()
    },
    beforeUnmount() {
        this.updateStoreData()
    },
    methods: {
        // IMPEDE QUE O TEXTO DO LABEL DASS OPÇÕES OU CRITÉRIOS SEJAM BRANCOS
        noBlankText(vetor, texto) {
            vetor.forEach((element, index) => {
                if (element === "") {
                    vetor[index] = texto + (index + 1).toString()
                }
            })
        },
        updateStoreData() {
            try {
                // Labels
                this.$store.dispatch("changeCriteriosLabelPrimeira", this.criteriosLabelPrimeira)
                this.$store.dispatch("changeOptionsLabelPrimeira", this.optionsLabelPrimeira)
                this.$store.dispatch("changeCriteriosLabelSegunda", this.criteriosLabelPrimeira)
                this.$store.dispatch("changeOptionsLabelSegunda", this.optionsLabelPrimeira)
                // Simbolos
                this.$store.dispatch("changeCriteriosSimboloPrimeira", this.criteriosSimboloPrimeira)
                this.$store.dispatch("changeOptionsSimboloPrimeira", this.optionsSimboloPrimeira)
                this.$store.dispatch("changeCriteriosSimboloSegunda", this.criteriosSimboloPrimeira)
                this.$store.dispatch("changeOptionsSimboloSegunda", this.optionsSimboloPrimeira)
            } catch (error) {
                console.log("falha no armazenamento", error)
            }
        },
        addCriterio() {
            if (this.criteriosLabelPrimeira.length < 9) {
                this.criteriosLabelPrimeira.push(`Criterio-${this.criteriosLabelPrimeira.length + 1}`)
                this.criteriosSimboloPrimeira.push(`C${this.criteriosSimboloPrimeira.length + 1}`)
            } else {
                window.alert("Você atingiu o limite de inputs para o método!")
            }
        },
        removeCriterio(index) {
            if (this.criteriosLabelPrimeira.length > 3) {
                this.criteriosLabelPrimeira.splice(index, 1)
                this.criteriosSimboloPrimeira.splice(index, 1)
            } else {
                window.alert("Você deve análisar no mínimo 3 critérios!")
            }
        },
        addOption() {
            if (this.optionsLabelPrimeira.length < 5) {
                this.optionsLabelPrimeira.push(`Opção-${this.optionsLabelPrimeira.length + 1}`)
                this.optionsSimboloPrimeira.push(`O${this.optionsSimboloPrimeira.length + 1}`)
            } else {
                window.alert("Você atingiu o limite de opções para o método!")
            }
        },
        removeOption(index) {
            if (this.optionsLabelPrimeira.length > 3) {
                this.optionsLabelPrimeira.splice(index, 1)
                this.optionsSimboloPrimeira.splice(index, 1)
            } else {
                window.alert("Você deve análisar no mínimo 3 opções!")
            }
        }
    }
}
</script>
<style scoped>

    .section-inputs{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        min-height: 600px;
        max-height: 600px;
    }
    .container-options, .container-criterios{
        width: 50%;
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;
    }
    .container-criterios div, .container-options div{
        display: flex;
        align-items: baseline;
        justify-content: start;
        max-height: 25px;
    }
    .input-simbolo{
        border-radius: 20px;
        text-align: center;
        width: 20%;
    }
    .label-simbolo{
        width: 31.5%;
    }
    .input-label{
        border-radius: 20px;
        text-align: center;
        width: 60%;
    }
    .label-nome{
        width:51%;
    }

    .label-nome, .label-simbolo{
        font-weight: bold;
        text-align: center;
    }
    .remove-button, .add-button{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        height: 18px;
        width: 18px;
        border: var(--borda-simples);
        border-radius: 50%;
        transform: translateY(1px);
        font-size: 12pt;
        background-color: var(--cor-tema-alt);
        color: var(--cor-texto-tema);
    }
    .add-button:hover, .remove-button:hover {
        cursor: pointer;
        box-shadow: 0 0 10px var(--cor-tema);
    }

</style>