<template>

    <switchLanguage
        class="switch-language"
        id="switch-language-mcdm"
    />

    <vueInputsPersonalizado/>

</template>

<script>
import vueInputsPersonalizado from "@/components/inputsEtapa.vue"
import criaSlideresMixin from "@/components/mixins/criaSlideres.vue"
import switchLanguage from "@/components/switchLanguage.vue"
import vueBalao from "@/components/balao.vue"


export default {
    name: "vue-view-mcdm-inputs-etapa",
    components: {
        vueInputsPersonalizado,
        switchLanguage
    },
    mixins: [
        criaSlideresMixin,
        vueBalao
    ],
    mounted() {
        try {
            this.criaSlideresPrimeira()
            this.criaSlideresSegunda()
        } catch {
            console.log("falha ao criar slideres (normal na troca de métodos)")
        }
    },
    beforeUnmount() {
        try {
            this.criaSlideresPrimeira()
            this.criaSlideresSegunda()
        } catch {
            console.log("falha ao criar slideres (normal na troca de métodos)")
        }
    }
}
</script>
<style scoped>
.switch-language{
    position: absolute;
    margin-top: -14%;
    opacity: 1;
}

</style>